function wc_ajaxAddToCart() {
    event.preventDefault();

    const eventTarget = event.currentTarget || event.target;
    
    if (!eventTarget) { return; }

    const productContainer = eventTarget.closest(".productContainer");
    const productIDCotnainer = document.getElementById("productIDContainer");
    const productQantityCotnainer = document.getElementById("productQantityCotnainer");
    const productVariationIDCotnainer = document.getElementById("productVariationIDCotnainer");

    if (!productContainer || !productIDCotnainer || !productQantityCotnainer || !productVariationIDCotnainer) { return; }
    const productID = productIDCotnainer.value;
    const productSKU = "";
    const productQuantity = productQantityCotnainer.value;
    const productVariationID = productVariationIDCotnainer.value;

    const data = {
        action : "ql_woocommerce_ajax_add_to_cart",
        productID : productID,
        productSKU : productSKU,
        productQuantity : productQuantity,
        productVariationID : productVariationID
    };

    $.ajax ({
        type : "post",
        url: wc_add_to_cart_params.ajax_url,
        data : data,
        beforeSend : function (response) {
            console.log(response);
        },
        complete : function (response) {
            console.log(response);
        },
        success : function (response) {
            if (response.error && response.product_url) {
                window.location = response.product_url;

                return;

            } else {
                $(document.body).trigger("added_to_cart", [response.fragments, response.cart_hash, eventTarget]);
            }
        }
    })

}